import ajax from '@/wrapper/ajax'

export default {
  methods: {
    getPagedFigure (userOid, figureName, page, size) {
      return ajax('GET', '/api/figure/paged', null, null, {
        userOid: userOid,
        figureName: figureName,
        page: page,
        size: size
      })
    },
    getPagedWishFigure (userOid, figureName, page, size) {
      return ajax('GET', '/api/figure/paged/wishlist', null, null, {
        userOid: userOid,
        figureName: figureName,
        page: page,
        size: size
      })
    },
    createFigure (userOid, figureIn) {
      return ajax('POST', '/api/figure', figureIn, null, {
        userOid: userOid
      })
    },
    getProgressInfo (userOid) {
      return ajax('GET', '/api/figure/progress-info', null, null, {
        userOid: userOid
      })
    },
    updateFigure (userOid, figureUpdateIn) {
      return ajax('PUT', '/api/figure', figureUpdateIn, null, {
        userOid: userOid
      })
    },
    revertFigureToWish (userOid, figureOid) {
      return ajax('PATCH', '/api/figure/revert-to-wish', null, null, {
        userOid: userOid,
        figureOid: figureOid
      })
    },
    deleteFigure (userOid, figureOid) {
      return ajax('DELETE', '/api/figure', null, null, {
        userOid: userOid,
        figureOid: figureOid
      })
    },
    getMonthlyFigureInfo (userOid) {
      return ajax('GET', '/api/figure/monthly-figure-info', null, null, {
        userOid: userOid
      })
    },
    getMonthlyFigureInfoDetail (userOid, year, month) {
      return ajax('GET', '/api/figure/monthly-figure-info/detail', null, null, {
        userOid: userOid,
        year: year,
        month: month
      })
    }
  }
}
