//     header: loadLayout('main-navbar')
//   }
// }

function homeMain (view) {
  return {
    default: loadView(view)
  }
}

const parent = (parentName) => {
  return {
    default: {
      name: parentName,
      template: '<router-view></router-view>'
    },
    header: loadLayout('main-navbar'),
    footer: loadLayout('main-footer')
  }
}

//    (url )
